<template>
  <v-container fill-height fluid class="auth">
    <v-row justify="center">
      <v-col cols="12"
      sm="8"
      md="3"
      lg="3">
      <v-form
        ref="form"
        @submit.prevent="login()"
        v-model="valid"
        lazy-validation
      >
        <v-card-text align-center>
          <v-row justify="center">
            <v-col>
              <v-img
                :src="require('../assets/bfi.png')"
                class="ml-auto mr-auto text-center justify-center"
                contain
                width="250px"
              />
            </v-col>
          </v-row>
          <br>
          <v-text-field
            mt-6
            dark
            type="email"
            v-model="state.form.email"
            :rules="emailRules"
            label="E-Mail"
            required
            dense
            outlined shaped></v-text-field>

          <v-text-field
            dark
            v-model="state.form.password"
            :rules="passwordRules"
            required
            label="Password"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            :type="show ? 'text' : 'password'"
            @click:append="show = !show"
            dense
            outlined>
          </v-text-field>
            <v-row
            v-if="state"
              no-gutters
            >
              <v-col cols="12" class="bg-white">
                <v-img
                  :loading="true"
                  :disabled="true"
                  :src="state.captcha.img"
                  style="cursor: pointer;"
                  class="mt-0"
                  @click="reqCaptcha()"
                  height="37px"
                ></v-img>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Captcha"
                  class="mt-6"
                  dark
                  :rules="captchaRules"
                  required
                  v-model="state.form.captcha"
                  dense
                  outlined></v-text-field>
              </v-col>
            </v-row>
            <v-btn
              block
              color="primary"
              type="submit"
              :loading="loading"
            >
            M A S U K
            <v-icon right>mdi-login</v-icon>
            </v-btn>
            <v-btn to="/Forgot-password" class="mt-4" block color="white" text>
              Lupa Password
            </v-btn>
          </v-card-text>
          </v-form>
     </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
export default {
  name: 'LoginView',
  data () {
    return {
      valid: true,
      show: false,
      // form: {
      //   email: '',
      //   password: '',
      //   captcha: '',
      //   config: 'flat'
      // },
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      passwordRules: [
        v => !!v || 'Password is required',
        v => v.length > 5 || 'Password min 6 characters'
      ],
      captchaRules: [
        v => !!v
      ]
    }
  },
  computed: {
    ...mapState('auth', {
      state: (state) => state,
      loading: (state) => state.loading
    })
  },
  mounted: function () {
    store.dispatch('auth/reqCaptcha')
  },
  methods: {
    validate () {
      this.$refs.form.validate()
    },
    ...mapActions('auth', [
      'login', 'reqCaptcha'
    ])
  }
}
</script>
<style>
.center-me {
  position: absolute;
  margin-left: 50%;
  transform: translate(-50%, 0);
}
</style>
